import { Component, OnInit } from '@angular/core';
import { Devices} from '../device_table';
import { StatusService } from '../status.service';

@Component({
  selector: 'app-device-table',
  templateUrl: './device-table.component.html',
  styleUrls: ['./device-table.component.scss']
})
export class DeviceTableComponent implements OnInit {

  devicesArr: Devices[];

  constructor(private statusService: StatusService) { }

  ngOnInit() {
    this.getDevices();
  }
  getDevices(): void {
    this.statusService.getDevices().subscribe( devices => this.devicesArr = devices);
  }

  delete( devices: Devices): void {
    this.devicesArr = this.devicesArr.filter( d => d !== devices);
    this.statusService.deleteDevices(devices).subscribe();

  }

}
